import React from 'react';
import Swal from 'sweetalert2';

function ProjectItem({ title, imageOne, imageTwo, desk, deskTitle }) {
  const popupInfo = () => {
    Swal.fire({
      title: ` ${title}`,
      text: ` ${deskTitle}`,
      imageUrl: `${imageTwo}`,
      imageWidth: 510,
      imageHeight: 350,
    });
  };

  return (
    <div className="menuItem">
      <div style={{ backgroundImage: `url(${imageOne})` }}> </div>
      <h1> {title} </h1>
      <h1> {desk} </h1>
      <button onClick={() => popupInfo()}>View</button>
    </div>
  );
}

export default ProjectItem;
