import React from 'react';
import { ProjectList } from '../helpers/ProjectList';
import ProjectItem from '../components/ProjectItem';
import '../styles/Project.css';

function Project() {
  return (
    <div className="menu">
      <h1 className="menuTitle">Portfolio</h1>
      <div className="menuList">
        {ProjectList.map((projectItem, key) => {
          return <ProjectItem key={key} title={projectItem.title} imageOne={projectItem.imageOne} imageTwo={projectItem.imageTwo} desk={projectItem.desk} deskTitle={projectItem.deskTitle} />;
        })}
      </div>
    </div>
  );
}

export default Project;
